.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30vh;
}
.footer-image {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(30vh);
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 0.7s;
}
.footer-image.active {
  transform: translateY(0);
}
.footer-text {
  position: absolute;
  background: orange;
  width: 100%;
  height: 50px;
  bottom: 0;
}