.header-nav {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 10px 30px;
  background: rgba(255, 255, 255, 0.9);
  transition-duration: 0.3s;
}
.nav-item {
  width: 12%;
}
.nav-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
.nav-item a {
  text-decoration: none;
  text-align: center;
}
.nav-item h1 {
  padding-bottom: 5px;
}
.nav-item img {
  width: 60px;
  height: 60px;
  margin-left: auto;
  margin-right: auto;
}